.wrap {
    min-width: 320px;
    box-sizing: border-box;
}

.container {
    .inner {
        max-width: 375px;
        min-width: 320px;
        margin: 0 auto;
        box-sizing: border-box;
    }
}

// spot
.spot {

    .inner {
        position: relative;
        padding: 24px 20px 10px;
    }

    .sub_title {
        font-size: 18px;
        line-height: 40px;

        text-align: center;
        color: #0dcb67;
    }

    .title {
        font-family: 'SeuratProB';
        font-size: 36px;
        line-height: 40px;
        text-align: center;

        span {
            position: relative;
            padding-left: 40px;

            &::before {
                position: absolute;
                top: 5px;
                left: 0;
                content: '';
                width: 30px;
                height: 32px;
                background: url("../img/main_mark.png") 0 0 no-repeat;
                background-size: 30px 32px;
                -webkit-background-size: 30px 32px;
            }
        }

    }

    .text {
        margin: 8px 0 10px;
        font-family: 'HelveticaNeue','NotoSansMediumJP';
        font-size: 16px;
        line-height: normal;
        color: #666;
    }

    .video_box {
        width: 100%;
        height: 188px;
        margin: 0 auto;
    }
}

.info {
    position: relative;
    background-color: #d5f1de;

    &::before {
        background-color: #d5f1de;
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: -57px;
        height: 57px;
        z-index: -10;
    }

    .inner {
        padding: 10px 20px 32px;
    }

    .title {
        height: 52px;
        background: url("../img/info_title.png") 50% 0 no-repeat;
        background-size: contain;
        -webkit-background-size: contain;
    }

    .text {
        font-family: 'HelveticaNeue','NotoSansRegularJP';
        font-size: 16px;
        font-weight: normal;

        color: #666;
    }

    .button_box {
        position: relative;
        display: block;
        width: 100%;
        padding: 17px 20px;
        background-color: #fff;
    
        @include border-radius(15px);

        & + .button_box {
            margin-top: 8px;
        }

        &.download {
            margin-top: 12px;

            &::after {
                content: '';
                position: absolute;
                top: 33px;
                right: 18px;
                width: 26px;
                height: 26px;
                background: url("../img/btn_download.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }

        &.share {
            margin-bottom: 29px;

            &::after {
                content: '';
                position: absolute;
                top: 33px;
                right: 18px;
                width: 26px;
                height: 26px;
                background: url("../img/btn_share.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }

            span {
                display: block;
                text-align: left;
                padding-right: 26px;
                font-size: 16px;
                font-family: 'SeuratProB';
                color: #444a61;
            }
    
            em {
                display: block;
                text-align: left;
                padding-right: 26px;
                font-size: 16px;
                color: #0dcb67;
            }
    

    }

    

    .sub_title {
        font-family: 'SeuratProB';
        font-size: 18px;
        color: #222;
    }

    .sub_text {
        font-family: 'HelveticaNeue','NotoSansRegularJP';
        margin-bottom: 24px;
        font-size: 16px;
        color: #666;

        em {
            font-family: 'NotoSansBoldJP';
            font-weight: 800;
            font-size: 16px;
            color: #0dcb67;
        }
    }

    .info_list {
        margin-top: 10px;

        .list_item {
            position: relative;
            padding-left: 74px;

            & + .list_item {
                margin-top: 17px;
            }

            &.item_1::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_1.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            &.item_1::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_1.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            &.item_2::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_2.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            &.item_3::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_3.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            &.item_4::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_4.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            &.item_5::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 60px;
                height: 60px;
                background: #fff url("../img/ico_5.png") no-repeat 50% 50%;
                background-size: 34px 34px;
                -webkit-background-size: 34px 34px;
                @include border-radius(50%);
            }

            p {
                font-family: 'SeuratProB';
                font-size: 14px;
                line-height: 19.2px;

                color: #333;

                em {
                    color: #0dcb67;
                }
            }

            span {
                font-size: 13px;
                color: #8ca293;
            }
        }
    }
}

// guide
.guide {
    background-color: #fff;

    .inner {
        padding: 30px 20px;
    }

    .title {
        height: 52px;
        background: url("../img/guide_title.png") 50% 0 no-repeat;
        background-size: contain;
        -webkit-background-size: contain;
    }

    .text {
        margin-bottom: 13px;
        font-family: 'HelveticaNeue','NotoSansRegularJP';
        font-size: 16px;
        font-weight: normal;
        color: #666;
        text-align: left;
    }

    .guide_list {
        text-align: center;

        .list_item {
            background-color: #e6f9ec;
            padding: 22px 20px 18px;
            @include border-radius(15px);

            & + .list_item {
                margin-top: 10px;
            }
        }

        .list_title {
            display: inline-block;
            position: relative;
            margin-bottom: 15px;
            padding-left: 35px;
            font-family: 'SeuratProB';

            font-size: 20px;
            line-height: 28px;
            color: #333;

            &.basic_des::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 28px;
                height: 28px;
                background: url("../img/book_mark.png") no-repeat 0 0;
                background-size: 28px 28px;
                -webkit-background-size: 28px 28px;
            }

            &.guide_des::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 28px;
                height: 28px;
                background: url("../img/band_mark.png") no-repeat 0 0;
                background-size: 28px 28px;
                -webkit-background-size: 28px 28px;
            }

            &.video::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 28px;
                height: 28px;
                background: url("../img/play_mark.png") no-repeat 0 0;
                background-size: 28px 28px;
                -webkit-background-size: 28px 28px;
            }
        }

        .list_text {
            font-family: 'HelveticaNeue','NotoSansRegularJP';
            text-align: left;
            margin-bottom: 12px;
            font-size: 15px;
            color: #333;

            &.img_des {
                position: relative;
                overflow: hidden;
                padding-right: 155px;

                &::after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: -4px;
                    transform: translateY(-50%);
                    width: 155px;
                    height: 168px;
                    background: #fff url("../img/book_cover.png") no-repeat 0 0;
                    background-size: 155px 168px;
                    -webkit-background-size: 155px 168px;
                }
            }
        }

        .video_box {
            width: 100%;
            height: 166px;
            margin: 0 auto;
        }

        .down_load_btn {
            display: block;
            width: 100%;
            margin: 14px auto 0;
            padding: 10px 0;
        
            @include border-radius(50px);
            background-color: #0dcb67;
        }

        .down_text {
            display: inline-block;
            position: relative;
            padding-right: 30px;
            font-family: 'SeuratProB';

            font-size: 18px;
            color: #fff;

            &::after {
                content: '';
                position: absolute;
                top: 6px;
                right: 0;
                width: 26px;
                height: 26px;
                background: url("../img/btn_download_wh.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }
    }

    .important_list_title {
        display: block;
        margin-bottom: 5px;
        font-weight: bold;
        font-size: 14px;
        color: #0dcb67;
        text-align: left;
    }
}

.important_list_box {
    text-align: left;
    @include clear;

    .important_list {
        float: left;
        width: 50%;
        margin-bottom: 11px;

        .list_item {
            position: relative;
            padding: 0 0 0 6px;
            font-size: 13px;
            line-height: 15px;
            color: #666;

            & + .list_item {
                margin-top: 0;
            }

            &::before {
                content: '';
                position: absolute;
                top: 8px;
                left: 0;
                width: 3px;
                height: 3px;
                background-color: #0dcb67;
                @include border-radius(50%);
            }
        }
    }
}

// more_info
.more_info {
    background-color: #f3f6f9;

    .inner {
        padding: 30px 20px;
    }

    .title {
        height: 52px;
        background: url("../img/more_info_title.png") 50% 0 no-repeat;
        background-size: contain;
        -webkit-background-size: contain;
    }

    .sub_title {
        margin-top: 30px;
        font-family: 'SeuratProB';
        font-size: 18px;
        line-height: 26px;
        letter-spacing: -0.6px;
        color: #222;
    }

    .text {
        font-family: 'HelveticaNeue','NotoSansRegularJP';
        margin-bottom: 12px;
        font-size: 16px;
        font-weight: normal;
        color: #666;
    }

    .download_list {
        .list_item {
            background-color: #e4eaf1;
            padding: 22px 20px 18px;
            @include border-radius(15px);

            & + .list_item {
                margin-top: 10px;
            }
        }

        .list_title {
            margin-bottom: 15px;
            font-family: 'NotoSansBoldJP';
            font-size: 18px;
            line-height: 24px;
            text-align: center;
            color: #333;
        }

        .list_text {
            font-family: 'HelveticaNeue','NotoSansMediumJP';
            margin-bottom: 12px;
            font-size: 14px;
            color: #666;
        }

        .down_load_btn {
            display: block;
            width: 100%;
            margin: 14px auto 0;
            padding: 10px 0;
        
            @include border-radius(50px);
            background-color: #0dcb67;
        }

        .down_text {
            display: inline-block;
            position: relative;
            padding-right: 30px;
            font-family: 'SeuratProB';

            font-size: 18px;
            color: #fff;

            &::after {
                content: '';
                position: absolute;
                top: 6px;
                right: 0;
                width: 26px;
                height: 26px;
                background: url("../img/btn_download_wh.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }
    }

    .btn {
        width: 100%;
        height: 60px;
    
        background-color: #fff;
        box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.14);
        @include border-radius(15px);

        & + .btn {
            margin-top: 8px;
        }

        &.share {
            position: relative;
            margin-top: 15px;
            padding: 0 46px 0 20px;
            background: url("../img/btn_share_text.png") 20px 20px no-repeat;
            background-size: 202px 15px;
            -webkit-background-size: 202px 15px;

            &::after {
                content: '';
                position: absolute;
                top: 15px;
                right: 20px;
                width: 26px;
                height: 26px;
                background: url("../img/btn_share.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }

        &.mail {
            position: relative;
            margin-bottom: 32px;
            padding: 0 46px 0 20px;
            background: url("../img/btn_mail_text.png") 20px 20px no-repeat;
            background-size: 170px 15px;
            -webkit-background-size: 170px 15px;

            &::after {
                content: '';
                position: absolute;
                top: 15px;
                right: 20px;
                width: 26px;
                height: 26px;
                background: url("../img/btn_mail.png") 0 0 no-repeat;
                background-size: 26px 26px;
                -webkit-background-size: 26px 26px;
            }
        }
    }

    .btn_download {
        display: inline-block;
        position: relative;
        width: 335px;
        height: 72px;
    
        background-color: #444a61;
        @include border-radius(50px);
        padding: 17px 43px 19px 75px;
        font-family: 'SeuratProB';
        font-size: 18px;
        color: #fff;

        .mo_320 {
            display: none;
        }

        .important {
            color: #0dcb67;
        }

        &::before {
            content: '';
            position: absolute;
            top: 14px;
            left: 25px;
            width: 42px;
            height: 43px;
            background: url("../img/ico_band.png") 0 0 no-repeat;
            background-size: 42px 43px;
            -webkit-background-size: 42px 43px;
        }

        &::after {
            content: '';
            position: absolute;
            top: 29px;
            right: 26px;
            width: 14px;
            height: 14px;
            background: url("../img/ico_arrow_wh.png") 0 0 no-repeat;
            background-size: 14px 14px;
            -webkit-background-size: 14px 14px;
        }

    }

}

// footer
.footer {
    background-color: #f3f6f9;

    .inner {
        border-top: 1px solid #e1e5ea;
        padding: 23px 48px;
        text-align: center;
    }

    .text {
        font-family: 'HelveticaNeue','NotoSansRegularJP';
        font-size: 14px;
        text-align: center;
        color: #666;
    }

    .footer_link {
        display: inline-block;
        text-align: center;
        padding-right: 10px;
        vertical-align: middle;
        color: #0dcb67;

        &::after {
            content: '';
            display: inline-block;
            width: 10px;
            height: 10px;
            vertical-align: middle;
            background: url("../img/ico_arrow_gr.png") 0 0 no-repeat;
            background-size: 10px 10px;
            -webkit-background-size: 10px 10px;
        }
    }
}

@media screen and (max-width: 374px) {
    .more_info {
        .btn_download {
            width: 100%;
            padding: 14px 70px 14px 80px;
            font-family: 'SeuratProB';
            font-size: 18px;
            line-height: 22px;
            text-align: left;

            .mo_320 {
                display: block;
            }
        }
    }
}


// popup_download
.popup.download {
    display: none;
    @include fixed_zero;
}

.popup.download.show {
    display: block;

    .popup_dimmed {
        content: '';
        @include absolute_zero;
        background-color: #000;
        opacity: 0.4;
    }

    .popup_layer {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        max-width: 355px;
        min-width: 280px;
        padding: 27px 16px 16px;
        background-color: #fff;
        @include border-radius(15px);
    }

    .popup_title {
        display: block;
        font-family: 'Pretendard';
        text-align: center;
    }
